import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';

import { AppComponent } from './app.component';

import { HeaderComponent } from './header/header.component';
import { WhatwedoSectionComponent } from './sections/whatwedo-section.component';
import { TestimonialsSectionComponent } from './sections/testimonials-section.component';
import { ProductSectionComponent } from './sections/product-section.component';
import { AboutSectionComponent } from './sections/about-section.component';
import { ContactSectionComponent } from './sections/contact-section.component';

@NgModule({
  declarations: [
    AppComponent,
    AboutSectionComponent,
    ContactSectionComponent,
    HeaderComponent,
    ProductSectionComponent,
    TestimonialsSectionComponent,
    WhatwedoSectionComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



